<template>
    <div class="shelf-footer" v-show="isEditMode">
        <div
            class="shelf-footer-tab-wrapper"
            v-for="item in tabs"
            :key="item.index"
            @click="onTabClick(item)"
        >
            <div
                class="shelf-footer-tab"
                :class="{ 'is-selected': isSelected }"
            >
                <div
                    class="icon-private tab-icon"
                    v-if="item.index === 1 && !isPrivate"
                ></div>
                <div
                    class="icon-private-see tab-icon"
                    v-if="item.index === 1 && isPrivate"
                ></div>
                <div
                    class="icon-download tab-icon"
                    v-if="item.index === 2 && !isDownload"
                ></div>
                <div
                    class="icon-download-remove tab-icon"
                    v-if="item.index === 2 && isDownload"
                ></div>
                <div class="icon-move tab-icon" v-if="item.index === 3"></div>
                <div class="icon-shelf tab-icon" v-if="item.index === 4"></div>
                <div
                    class="tab-text"
                    :class="{ 'remove-text': item.index === 4 }"
                >
                    {{ label(item) }}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { removeLocalStorage, saveBookShelf } from '../../utils/localStorage'
import { storeShelfMixin } from '../../utils/mixin'
import { download } from '../../api/store'
import CreateToast from '../common/Toast.js'
import { removeLocalForage } from '../../utils/localForage'
export default {
    mixins: [storeShelfMixin],
    computed: {
        isSelected() {
            return this.shelfSelected && this.shelfSelected.length > 0
        },
        tabs() {
            return [
                {
                    label: this.$t('shelf.private'),
                    label2: this.$t('shelf.noPrivate'),
                    index: 1
                },
                {
                    label: this.$t('shelf.download'),
                    label2: this.$t('shelf.delete'),
                    index: 2
                },
                {
                    label: this.$t('shelf.move'),
                    index: 3
                },
                {
                    label: this.$t('shelf.remove'),
                    index: 4
                }
            ]
        },
        // 判断选中的图书是不是私密阅读状态
        isPrivate() {
            if (!this.isSelected) {
                return false
            } else {
                // 选中的每本书都是私密状态才是true
                return this.shelfSelected.every(item => item.private)
            }
        },
        isDownload() {
            if (!this.isSelected) {
                return false
            } else {
                // 选中的每本书下载过才为true
                return this.shelfSelected.every(item => item.cache)
            }
        }
    },
    methods: {
        omComplete() {
            this.setShelfFooterShowVisible(false) // 点击后出现的框框
            this.setIsEditMode(false)
            // 再保存到localstorage
            saveBookShelf(this.shelfList)
        },
        // 实际下载
        downloadBook(book) {
            let toast = new CreateToast('等待下载中....')
            return new Promise((resolve, reject) => {
                // download api
                download(
                    book,
                    book => {
                        toast.setTimeUnmount(1000)
                        resolve(book)
                    },
                    reject,
                    ProgressEvent => {
                        const progress =
                            Math.floor(
                                (ProgressEvent.loaded / ProgressEvent.total) *
                                    100
                            ) + '%'
                        const text = this.$t('shelf.progressDownload').replace(
                            '$1',
                            `${book.fileName}.epub(${progress})`
                        )
                        toast.myUnmount() // 这样效率肯定很低，不停的卸载安装
                        toast = new CreateToast(text)
                    }
                )
            })
        },
        // 同步
        async downloadSelectedBook() {
            // indexDB数据库
            for (let i = 0; i < this.shelfSelected.length; i++) {
                await this.downloadBook(this.shelfSelected[i]).then(book => {
                    book.cache = true
                })
            }
        },
        // 删除离线缓存
        removeSelectedBook() {
            Promise.all(
                this.shelfSelected.map(book => this.removeBook(book))
            ).then(books => {
                books.map(book => {
                    book.cache = false
                })
                saveBookShelf(this.shelfList)
            })
        },
        removeBook(book) {
            return new Promise((resolve, reject) => {
                removeLocalStorage(
                    `${book.categoryText}/$t{book.fileName}-info`
                ) // 删除该书的各种配置
                removeLocalForage(`${book.fileName}`) // 删除该书的缓存
                resolve(book)
                const toast = new CreateToast(
                    this.$t('shelf.removeDownloadSuccess')
                )
                toast.setTimeUnmount(1000)
            })
        },
        setPrivate() {
            // 把图书的字段置为false，模拟，线上还需要传回后端
            let isPrivate
            if (this.isPrivate) {
                isPrivate = false
            } else {
                isPrivate = true
            }
            // shelfSelected都是从shelfList中拿过来的，相当于修改了shelfList中的数据
            this.shelfSelected.forEach(book => {
                book.private = isPrivate
            })
            this.omComplete()
            // 显示信息
            if (isPrivate) {
                this.$emit('showToast', this.$t('shelf.setPrivateSuccess'))
            } else {
                this.$emit('showToast', this.$t('shelf.closePrivateSuccess'))
            }
        },
        // async await同步执行
        async setDownload() {
            // 把图书的字段置为false，模拟，线上还需要传回后端
            this.omComplete()
            if (this.isDownload) {
                this.removeSelectedBook()
            } else {
                await this.downloadSelectedBook()
                saveBookShelf(this.shelfList)
                setTimeout(() => {
                    const toast = new CreateToast(
                        this.$t('shelf.setDownloadSuccess')
                    )
                    toast.setTimeUnmount(1000)
                }, 1500)
            }
        },
        // 从书架中删除书籍，但没有影响到后端
        removeSelected() {
            this.shelfSelected.forEach(selected => {
                this.setShelfList(
                    this.shelfList.filter(book => {
                        if (book.type === 2) {
                            book.itemList = book.itemList.filter(subBook => {
                                return subBook !== selected
                            })
                            return true
                        } else if (book === selected) return false
                        else return true
                    })
                )
            })
            this.setShelfSelected([])
            this.omComplete()
        },
        showPrivate() {
            this.setShelfFooterPopupProps({
                title: this.isPrivate
                    ? this.$t('shelf.closePrivateTitle')
                    : this.$t('shelf.setPrivateTitle'),
                btn: [
                    {
                        text: this.isPrivate
                            ? this.$t('shelf.close')
                            : this.$t('shelf.open'),
                        click: () => {
                            this.setPrivate()
                        }
                    },
                    {
                        text: this.$t('shelf.cancel'),
                        click: () => {
                            this.setShelfFooterShowVisible(false)
                        }
                    }
                ]
            })
            this.setShelfFooterShowVisible(true)
        },
        showDownload() {
            this.setShelfFooterPopupProps({
                title: this.isDownload
                    ? this.$t('shelf.removeDownloadTitle')
                    : this.$t('shelf.setDownloadTitle'),
                btn: [
                    {
                        text: this.isDownload
                            ? this.$t('shelf.delete')
                            : this.$t('shelf.open'),
                        click: () => {
                            this.setDownload()
                        }
                    },
                    {
                        text: this.$t('shelf.cancel'),
                        click: () => {
                            this.setShelfFooterShowVisible(false)
                        }
                    }
                ]
            })
            this.setShelfFooterShowVisible(true)
        },
        showRmove() {
            let title
            if (this.shelfSelected.length === 1) {
                title = this.$t('shelf.removeBookTitle').replace(
                    '$1',
                    `《${this.shelfSelected[0].title}》`
                )
            } else {
                title = this.$t('shelf.removeBookTitle').replace(
                    '$1',
                    this.$t('shelf.selectedBooks')
                )
            }
            this.setShelfFooterPopupProps({
                title: title,
                btn: [
                    {
                        text: this.$t('shelf.removeBook'),
                        type: 'danger',
                        click: () => {
                            this.removeSelected()
                        }
                    },
                    {
                        text: this.$t('shelf.cancel'),
                        click: () => {
                            this.setShelfFooterShowVisible(false)
                        }
                    }
                ]
            })
            this.setShelfFooterShowVisible(true)
        },
        onTabClick(item) {
            if (!this.isSelected) {
                return
            }
            switch (item.index) {
                case 1:
                    this.showPrivate()
                    break
                case 2:
                    this.showDownload()
                    break
                case 3:
                    this.$emit('showDialog')
                    break
                case 4:
                    this.showRmove()
                    break
                default:
                    break
            }
        },
        label(item) {
            switch (item.index) {
                case 1:
                    return this.isPrivate ? item.label2 : item.label
                case 2:
                    return this.isDownload ? item.label2 : item.label
                default:
                    return item.label
            }
        }
    }
}
</script>
<style lang="scss" scoped>
@import '@/assets/styles/global';
.shelf-footer {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 120;
    display: flex;
    width: 100%;
    height: px2rem(48);
    background: white;
    box-shadow: 0 px2rem(-2) px2rem(4) 0 rgba(0, 0, 0, 0.1);
    .shelf-footer-tab-wrapper {
        flex: 1;
        width: 25%;
        height: 100%;
        .shelf-footer-tab {
            width: 100%;
            height: 100%;
            @include columnCenter;
            opacity: 0.5;
            &.is-selected {
                opacity: 1;
            }
            .tab-icon {
                font-size: px2rem(20);
                color: #666;
            }
            .tab-text {
                margin-top: px2rem(5);
                font-size: px2rem(12);
                color: #666;
                &.remove-text {
                    color: $color-pink;
                }
            }
            .icon-shelf {
                color: $color-pink;
            }
        }
    }
}
</style>
