<template>
    <!-- 动态加载三种类型的组件 -->
    <div
        class="shelf-item"
        :class="{ 'shelf-item-shadow': data.type === 1 || data.type === 2 }"
        @click="onItemClick"
    >
        <component
            :is="item"
            :data="data"
            class="shelf-item-comp"
            :class="{ 'is-edit': isEditMode && data.type === 2 }"
        ></component>
        <div
            class="icon-selected"
            :class="{ 'is-selected': data.selected }"
            v-show="isEditMode && data.type === 1"
        ></div>
    </div>
</template>

<script>
import { storeShelfMixin } from '../../utils/mixin'
import shelfBook from './shelfItemBook'
import shelfCategory from './shelfItemCategory'
import shelfAdd from './shelfItemAdd'
import { gotoStoreHome } from '../../utils/store'

export default {
    mixins: [storeShelfMixin],
    props: {
        data: Object
    },
    components: {
        shelfBook,
        shelfCategory,
        shelfAdd
    },
    computed: {
        item() {
            if (this.data.type === 1) {
                return this.book
            } else if (this.data.type === 2) {
                return this.category
            } else {
                return this.add
            }
        }
    },
    data() {
        return {
            book: 'shelfBook',
            category: 'shelfCategory',
            add: 'shelfAdd'
            // childData: this.data // 但这个不持久，修改props的又不对
        }
    },
    methods: {
        onItemClick() {
            // 让它先只能编辑普通书籍，分组情况先不考虑
            if (this.isEditMode) {
                if (this.data.type === 1) {
                    // 编辑模式选中书籍，selected修改，要影响到vuex中的数据
                    // 本组件的数据修改了还得存入vuex中，vuex的数据也不可直接修改，应该通过mutation或action
                    const shelfListTemp = this.shelfList.map(item => {
                        if (item.type === 1 && item.id === this.data.id) {
                            item.selected = !this.data.selected
                        } else if (item.itemList) {
                            item.itemList.map(subItem => {
                                if (subItem.id === this.data.id) {
                                    subItem.selected = !this.data.selected
                                }
                                return subItem
                            })
                        }
                        return item
                    })
                    this.setShelfList(shelfListTemp).then(() => {
                        if (this.data.selected) {
                            this.shelfSelected.pushWithoutDuplicate(this.data) // 这个函数可以响应式修改
                        } else {
                            this.setShelfSelected(
                                this.shelfSelected.filter(
                                    item => item.id !== this.data.id
                                )
                            )
                        }
                    })
                }
                // 有个bug：选中分组时如何处理?
            } else {
                if (this.data.type === 1) {
                    // 跳到书籍详情
                    this.showBookDetail(this.data)
                } else if (this.data.type === 2) {
                    // 应该调到分组中
                    this.$router.push({
                        path: '/store/category',
                        query: {
                            title: this.data.title
                        }
                    })
                } else {
                    // 回到主页添加到书架
                    gotoStoreHome(this)
                }
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/global';
.shelf-item {
    position: relative;
    width: 100%;
    height: 100%;
    &.shelf-item-shadow {
        box-shadow: px2rem(2) px2rem(2) px2rem(6) px2rem(2)
            rgba(200, 200, 200, 0.3);
    }
    .shelf-item-comp {
        opacity: 1;
        &.is-edit {
            opacity: 0.5;
        }
    }
    .icon-selected {
        position: absolute;
        bottom: px2rem(2);
        right: px2rem(2);
        font-size: px2rem(18);
        color: rgba(0, 0, 0, 0.4);
        &.is-selected {
            color: $color-blue;
        }
    }
}
</style>
