<template>
    <transition name="fade">
        <div
            class="shelf-title"
            v-show="shelfTitleVisible"
            :class="{ 'hide-shadow': ifHideShadow }"
        >
            <div class="shelf-title-text-wrapper">
                <span class="shelf-title-text">{{ title }}</span>
                <span class="shelf-title-sub-text" v-show="isEditMode">
                    {{ selectedText }}
                </span>
            </div>
            <div
                class="shelf-titlbe-btn-wrapper shelf-title-left"
                v-if="showClear"
            >
                <span class="shelf-title-btn-text" @click="clearCache">
                    {{ $t('shelf.clearCache') }}
                </span>
            </div>
            <div
                class="shelf-titlbe-btn-wrapper shelf-title-right"
                v-if="showEdit"
            >
                <span class="shelf-title-btn-text" @click="onEditClick">
                    {{
                        isEditMode ? $t('shelf.cancel') : $t('shelf.edit')
                    }}</span
                >
            </div>
            <div
                class="shelf-titlbe-btn-wrapper shelf-title-left"
                v-if="showBack"
            >
                <span class="icon-back" @click="back"> </span>
            </div>
            <div
                class="shelf-titlbe-btn-wrapper"
                :class="{
                    'shelf-title-left': changeGroupLeft,
                    'shelf-title-right': changeGroupRight
                }"
                @click="changeGroup"
                v-if="showChangeGroup"
            >
                <span class="shelf-title-btn-text">
                    {{ $t('shelf.editGroup') }}
                </span>
            </div>
        </div>
    </transition>
</template>

<script>
import { clearLocalForage } from '../../utils/localForage'
import { clearLocalStorage, saveBookShelf } from '../../utils/localStorage'
import { storeShelfMixin } from '../../utils/mixin'
import CreateToast from '../common/Toast.js'
export default {
    mixins: [storeShelfMixin],
    props: {
        title: String
    },
    data() {
        return {
            ifHideShadow: true
        }
    },
    computed: {
        emptyCategory() {
            return (
                !this.shelfCategory ||
                !this.shelfCategory.itemList ||
                this.shelfCategory.itemList.length === 0
            )
        },
        showEdit() {
            return this.currentType === 1 || !this.emptyCategory
        },
        showClear() {
            return this.currentType === 1
        },
        showBack() {
            return this.currentType === 2 && !this.isEditMode
        },
        showChangeGroup() {
            return (
                this.currentType === 2 &&
                (this.isEditMode || this.emptyCategory)
            )
        },
        changeGroupLeft() {
            return !this.emptyCategory
        },
        changeGroupRight() {
            return this.emptyCategory
        },
        selectedText() {
            const selectedNumber = this.shelfSelected
                ? this.shelfSelected.length
                : 0
            if (selectedNumber <= 0) {
                return this.$t('shelf.selectBook')
            } else if (selectedNumber === 1) {
                return this.$t('shelf.haveSelectedBook').replace(
                    '$1',
                    selectedNumber
                )
            } else {
                return this.$t('shelf.haveSelectedBooks').replace(
                    '$1',
                    selectedNumber
                )
            }
        }
    },
    methods: {
        changeGroupName() {
            this.setShelfFooterShowVisible(false)
            this.setDialogProps({
                showNewGroup: true,
                groupName: this.title
            }).then(() => {
                this.$emit('showDialog')
            })
        },
        onComplete() {
            this.setShelfFooterShowVisible(false)
            // 删除分组
            this.setShelfList(
                this.shelfList.filter(book => book.id !== this.shelfCategory.id)
            ).then(() => {
                // 保存书架数据
                saveBookShelf(this.shelfList)
                this.setIsEditMode(false)
                this.$router.go(-1)
            })
        },
        deleteGroup() {
            // 把分组内的图书都移到外面书架；删除分组
            // 可以复用moveOutFromGroup函数
            if (!this.emptyCategory) {
                // 当前书架有书，全部置为选中状态
                this.setShelfSelected(this.shelfCategory.itemList)
                this.moveOutOfGroup(this.onComplete)
            } else {
                this.onComplete()
            }
        },
        showDeleteGroup() {
            this.setShelfFooterShowVisible(false).then(() => {
                setTimeout(() => {
                    this.setShelfFooterPopupProps({
                        title: this.$t('shelf.deleteGroupTitle'),
                        btn: [
                            this.createPopupBtn(
                                this.$t('shelf.confirm'),
                                () => {
                                    this.deleteGroup()
                                },
                                'danger'
                            ),
                            this.createPopupBtn(this.$t('shelf.cancel'), () => {
                                this.setShelfFooterShowVisible(false)
                            })
                        ]
                    }).then(() => {
                        this.setShelfFooterShowVisible(true)
                    })
                }, 200)
            })
        },
        createPopupBtn(text, onClick, type = 'normal') {
            // 把传入的参数封装在一个对象中返回
            return {
                text: text,
                type: type,
                click: onClick
            }
        },
        changeGroup() {
            const btn = [
                this.createPopupBtn(this.$t('shelf.editGroupName'), () => {
                    this.changeGroupName()
                }),
                this.createPopupBtn(
                    this.$t('shelf.deleteGroup'),
                    () => {
                        this.showDeleteGroup()
                    },
                    'danger'
                ),
                this.createPopupBtn(this.$t('shelf.cancel'), () => {
                    this.setShelfFooterShowVisible(false)
                })
            ]
            this.setShelfFooterPopupProps({
                title: this.$t('shelf.editGroup'),
                btn: btn
            }).then(() => {
                this.setShelfFooterShowVisible(true)
            })
        },
        back() {
            this.$router.go(-1)
            this.setIsEditMode(false)
        },
        onEditClick() {
            if (!this.isEditMode) {
                this.setShelfSelected([])
                // 这样直接修改vuex中shelfList的属性是否有问题？
                // this.shelfList.forEach(item => {
                //     item.selected = false
                // })
                const shelfListTemp = this.shelfList.map(item => {
                    item.selected = false
                    if (item.itemList) {
                        item.itemList.map(subItem => {
                            subItem.selected = false
                            return subItem
                        })
                    }
                    return item
                })
                this.setShelfList(shelfListTemp)
            }
            this.setIsEditMode(!this.isEditMode)
        },
        clearCache() {
            clearLocalStorage()
            clearLocalForage()
            this.setShelfList([])
            this.setShelfSelected([])
            this.getShelfList()
            const toast = new CreateToast(this.$t('shelf.clearCacheSuccess'))
            toast.setTimeUnmount(500)
        }
    },
    watch: {
        offsetY(offsetY) {
            if (offsetY > 0) {
                this.ifHideShadow = false
            } else {
                this.ifHideShadow = true
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/global';
.shelf-title {
    position: relative;
    width: 100%;
    height: px2rem(42);
    background: white;
    z-index: 130;
    box-shadow: 0 px2rem(2) px2rem(2) 0 rgba(0, 0, 0, 0.1);
    &.hide-shadow {
        box-shadow: none;
    }
    .shelf-title-text-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: px2rem(42);
        @include columnCenter;
        .shelf-title-text {
            font-size: px2rem(16);
            line-height: px2rem(20);
            font-weight: bold;
            color: #333;
        }
        .shelf-title-sub-text {
            font-size: px2rem(10);
            color: #333;
        }
    }
    .shelf-titlbe-btn-wrapper {
        position: absolute;
        top: 0;
        box-sizing: border-box;
        height: 100%;
        &.shelf-title-left {
            left: 0;
            padding-left: px2rem(15);
        }
        &.shelf-title-right {
            right: 0;
            padding-right: px2rem(15);
        }
        .shelf-title-btn-text {
            font-size: px2rem(14);
            color: #666;
        }
        .icon-back {
            font-size: px2rem(20);
            color: #666;
        }
    }
}
</style>
