<template>
    <div class="popup" v-if="showVisible">
        <transition name="fade">
            <div
                class="popup-bg"
                @click.stop.prevent="onclick"
                v-show="showVisible"
            ></div>
        </transition>
        <transition name="popup-slide-up">
            <div class="popup-wrapper" v-show="visible">
                <div class="popup-title" v-if="title && title.length > 0">
                    {{ title }}
                </div>
                <div
                    class="popup-btn"
                    v-for="(item, index) in btn"
                    :key="index"
                    @click="item.click"
                    :class="{ danger: item.type === 'danger' }"
                >
                    {{ item.text }}
                </div>
            </div>
        </transition>
    </div>
</template>
<script>
export default {
    name: 'popup',
    props: {
        title: String,
        btn: Array
    },
    data() {
        return {
            showVisible: false,
            visible: false // 调整动画
        }
    },
    methods: {
        show() {
            this.showVisible = true
            setTimeout(() => {
                this.visible = true
            }, 200)
        },
        hide() {
            this.visible = false
            setTimeout(() => {
                this.showVisible = false
            }, 200)
        },
        onclick() {
            this.$emit('hide') // 并触发告诉父组件这个组件关闭了
        }
    }
}
</script>
<style lang="scss" scoped>
@import '@/assets/styles/global';
.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    .popup-bg {
        width: 100%;
        height: 100%;
    }
    .popup-wrapper {
        position: fixed;
        left: 0;
        bottom: 0;
        z-index: 2000;
        width: 100%;
        background: white;
        .popup-title {
            width: 100%;
            height: px2rem(44);
            border-bottom: px2rem(1) solid #eee;
            font-size: px2rem(12);
            line-height: px2rem(14);
            padding: px2rem(15);
            box-sizing: border-box;
            color: #999;
            @include center;
        }
        .popup-btn {
            width: 100%;
            height: px2rem(60);
            border-bottom: px2rem(1) solid #eee;
            font-size: px2rem(16);
            color: #666;
            font-weight: bold;
            @include center;
            &.danger {
                color: $color-pink;
            }
        }
    }
}
</style>
