<template>
    <ebook-dialog :title="title" ref="dialog">
        <div class="dialog-list-wrapper" v-if="!ifNewGroup">
            <!-- 不是新增分组时，展示现有分组 -->
            <div v-for="(item, index) in categoryList" :key="index">
                <div
                    class="dialog-list-item"
                    :class="{ 'is-add': item.edit ? item.edit === 1 : false }"
                    @click="onGroupClick(item)"
                    v-if="
                        (item.edit === 2 && isInGroup) ||
                            item.edit !== 2 ||
                            !item.edit
                    "
                >
                    <div class="dialog-list-item-text">{{ item.title }}</div>
                    <div
                        class="dialog-list-icon-wrapper"
                        v-if="
                            isInGroup &&
                                shelfCategory &&
                                shelfCategory.id === item.id
                        "
                    >
                        <span class="icon-check"></span>
                    </div>
                </div>
            </div>
        </div>
        <div class="dialog-new-group-wrapper" v-else>
            <!-- 输入新分组的名字 -->
            <div class="dialog-input-title-wrapper">
                <span class="dialog-input-title">{{
                    $t('shelf.groupName')
                }}</span>
            </div>
            <div class="dialog-input-wrapper">
                <div class="dialog-input-inner-wrapper">
                    <input
                        type="text"
                        class="dialog-input"
                        v-model="newGroupName"
                        ref="dialogInput"
                    />
                    <div
                        class="dialog-input-clear-wrapper"
                        @click="clear"
                        v-show="newGroupName && newGroupName.length > 0"
                    >
                        <span class="icon-close-circle-fill"></span>
                    </div>
                </div>
            </div>
        </div>
        <template v-slot:btn>
            <div class="group-dialog-btn-wrapper">
                <div class="dialog-btn" @click="hide">
                    {{ $t('shelf.cancel') }}
                </div>
                <div
                    class="dialog-btn"
                    @click="createNewGroup"
                    v-if="ifNewGroup"
                    :class="{
                        'is-empty': !(newGroupName && newGroupName.length > 0)
                    }"
                >
                    {{ $t('shelf.confirm') }}
                </div>
            </div>
        </template>
    </ebook-dialog>
</template>

<script>
import EbookDialog from '@/components/common/Dialog'
import { storeShelfMixin } from '../../utils/mixin'
import CreateToast from '../common/Toast'
import { saveBookShelf } from '../../utils/localStorage'
import { appendAddToShelf, removeAddFromShelf } from '../../utils/store'

export default {
    name: 'grouo-dialog',
    mixins: [storeShelfMixin],
    components: {
        EbookDialog
    },
    data() {
        return {
            ifNewGroup: false,
            newGroupName: ''
        }
    },
    computed: {
        defaultCategory() {
            // edit是编辑方式，1新增，2移除，其余就是普通分组
            // 当在分组内部时才显示2
            return [
                {
                    title: this.$t('shelf.newGroup'),
                    edit: 1
                },
                {
                    title: this.$t('shelf.groupOut'),
                    edit: 2
                }
            ]
        },
        isInGroup() {
            return this.currentType === 2 // 当前是否在分组内部
        },
        category() {
            // shelfList中type===2才是分组
            return this.shelfList.filter(item => item.type === 2)
        },
        categoryList() {
            // 对话框展示的list
            return [...this.defaultCategory, ...this.category]
        },
        title() {
            // 对话框的标题
            return !this.ifNewGroup
                ? this.$t('shelf.moveBook')
                : this.$t('shelf.newGroup')
        }
    },
    methods: {
        show() {
            this.ifNewGroup = this.dialogProps.showNewGroup
            this.newGroupName = this.dialogProps.groupName
            this.$refs.dialog.show()
        },
        hide() {
            this.$refs.dialog.hide()
            setTimeout(() => {
                this.ifNewGroup = false
                this.setDialogProps({ showNewGroup: false, groupName: '' }) // 恢复最初设置
            }, 200)
        },
        clear() {
            this.newGroupName = ''
        },
        onGroupClick(item) {
            if (item.edit && item.edit === 1) {
                // 新建分组
                this.ifNewGroup = true
            } else if (item.edit && item.edit === 2) {
                // 移出分组，只有在进入分组时才会出现这个选项
                this.moveOutFromGroup(item)
            } else {
                // 移入分组
                this.moveToGroup(item)
            }
        },
        onComplete() {
            saveBookShelf(this.shelfList)
            this.hide()
            this.setIsEditMode(false)
        },
        moveOutFromGroup() {
            this.moveOutOfGroup(this.onComplete)
        },
        // group表示被选中分组中的电子书
        moveToGroup(group) {
            // 把需要移进分组的书籍从shelfList中删除，如果在分组中也需要判断
            // 第一步是对选中图书进行过滤
            this.setShelfList(
                this.shelfList.filter(book => {
                    if (book.itemList) {
                        book.itemList = book.itemList.filter(
                            subBook => this.shelfSelected.indexOf(subBook) < 0
                        )
                    }
                    return this.shelfSelected.indexOf(book) < 0
                })
            ).then(() => {
                // 第二步是把选中的图书加入到相应的分组中
                if (group && group.itemList) {
                    group.itemList = [...group.itemList, ...this.shelfSelected]
                }
                // 然后重新排序
                group.itemList.forEach((item, index) => {
                    item.id = index + 1
                })
                const toast = new CreateToast(
                    this.$t('shelf.moveBookInSuccess').replace(
                        '$1',
                        group.title
                    )
                )
                toast.setTimeUnmount(1000)
                this.onComplete()
            })
        },
        createNewGroup() {
            if (!this.newGroupName && this.newGroupName.length === 0) {
                return
            }
            if (this.dialogProps.showNewGroup) {
                // 打开dialog就是要修改名称时
                this.shelfCategory.title = this.newGroupName
                this.onComplete()
            } else {
                // 分组新增
                const group = {
                    id: this.shelfList[this.shelfList.length - 2].id + 1,
                    itemList: [],
                    selected: false,
                    title: this.newGroupName,
                    type: 2
                }
                // 先新增加分组，再往分组里添加书籍
                let list = removeAddFromShelf(this.shelfList)
                list.push(group)
                list = appendAddToShelf(list)
                this.setShelfList(list).then(() => {
                    // 再把选中的书移动到新分组中
                    // this.moveToGroup(group) // 系统无法响应，修改group并没有修改shelfList本身
                    this.moveToGroup(this.shelfList[this.shelfList.length - 2])
                    this.onComplete()
                })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '@/assets/styles/global';
.dialog-list-wrapper {
    width: 100%;
    padding: 0 px2rem(20);
    box-sizing: border-box;
    font-size: px2rem(14);
    @include scroll;
    .dialog-list-item {
        display: flex;
        padding: px2rem(15) 0;
        box-sizing: border-box;
        color: #666;
        &.is-add {
            color: $color-blue;
            &:active {
                color: $color-blue-transparent;
            }
        }
        &:active {
            color: rgba(102, 102, 102, 0.5);
        }
        .dialog-list-item-text {
            flex: 1;
        }
        .dialog-list-icon-wrapper {
            flex: 0 0 px2rem(30);
            color: $color-blue;
            @include right;
        }
    }
}
.dialog-new-group-wrapper {
    width: 100%;
    padding: 0 px2rem(20);
    box-sizing: border-box;
    .dialog-input-title-wrapper {
        font-size: px2rem(10);
        margin-top: px2rem(20);
    }
    .dialog-input-wrapper {
        width: 100%;
        padding: 0 0 px2rem(30) 0;
        box-sizing: border-box;
        .dialog-input-inner-wrapper {
            display: flex;
            width: 100%;
            padding: px2rem(10) 0;
            box-sizing: border-box;
            border-bottom: px2rem(1) solid #eee;
            font-size: px2rem(14);
            color: #666;
            .dialog-input {
                flex: 1;
                border: none;
                &:focus {
                    outline: none;
                }
            }
            .dialog-input-clear-wrapper {
                flex: 0 0 px2rem(30);
                color: #ccc;
                @include center;
                &:active {
                    color: #999;
                }
            }
        }
    }
}
.group-dialog-btn-wrapper {
    width: 100%;
    @include center;
}
.dialog-btn {
    flex: 1;
    &.is-empty {
        color: rgba(255, 255, 255, 0.5);
    }
    &:active {
        color: rgba(255, 255, 255, 0.5);
    }
}
</style>
